import React from 'react';
import { Helmet } from 'react-helmet-async';
import { useTranslation } from 'react-i18next';

export function Services() {
    const { t } = useTranslation();

    const servicesObject = [
        {
            key: 1,
            imgSrc: "https://img.icons8.com/?size=100&id=12447&format=png&color=ffffff",
            heading: 'services.softwareDevelopment',
            des: "services.softwareDescription",
        },
        {
            key: 2,
            imgSrc: "https://img.icons8.com/?size=100&id=24870&format=png&color=ffffff",
            heading: 'services.architectureAndDesign',
            des: "services.architectureDescription",
        },
        {
            key: 7,
            imgSrc: "https://img.icons8.com/?size=100&id=4pA6aw4OTuwp&format=png&color=ffffff",
            heading: 'services.aiAndMachineLearning',
            des: "services.aiDescription",
        },
        {
            key: 4,
            imgSrc: "https://img.icons8.com/?size=100&id=2zyvaojJtgOA&format=png&color=ffffff",
            heading: 'services.devOpsAndCloud',
            des: "services.devOpsDescription",
        },
        {
            key: 5,
            imgSrc: "https://img.icons8.com/?size=100&id=11678&format=png&color=ffffff",
            heading: 'services.securityServices',
            des: "services.securityDescription",
        },
        {
            key: 6,
            imgSrc: "https://img.icons8.com/?size=100&id=ZQ9axxszRfad&format=png&color=ffffff",
            heading: 'services.dataScienceAndAnalytics',
            des: "services.dataScienceDescription",
        },
        {
            key: 8,
            imgSrc: "https://img.icons8.com/?size=100&id=52230&format=png&color=ffffff",
            heading: 'services.productStrategy',
            des: "services.productStrategyDescription",
        },
        {
            key: 3,
            imgSrc: "https://img.icons8.com/?size=100&id=22136&format=png&color=ffffff",
            heading: 'services.qualityAssurance',
            des: "services.qualityDescription",
        },
    ];

    return (
        <>
            {/* Main Heading for Services */}
            <Helmet>
                <meta name="description" content={t("meta.description")} />
                <meta name="keywords" content={t("meta.keywords")} />
                <meta name="author" content={t("meta.author")} />
            </Helmet>
            <p className="text-3xl text-center font-bold py-20 md:text-4xl lg:text-5xl">
                {t('services.heading')}
            </p>
            <div className="grid sm:grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-6 lg:gap-16 text-white px-6 lg:px-24 pointer">
                {servicesObject?.map((service) => (
                    <div key={service.key} className="service-card border-2 px-2 border-[#5ecaf4] hover:border-2 rounded-xl h-auto py-8 transition-all duration-300">
                        <div className="h-28 flex justify-center items-center">
                            <img src={service.imgSrc} alt={t(service.heading)} className="w-24 h-24" />
                        </div>
                        <h1 className="text-center font-bold text-xl mt-4">{t(service.heading)}</h1>
                        <p className="text-center p-4 mx-4 text-sm">{t(service.des)}</p>
                    </div>
                ))}
            </div>

            <style>
                {`
                .service-card {
                    transition: all 0.6s ease-in-out;
                }
                .service-card:hover {
                    transform: scale(1.05); 
                }
                `}
            </style>
        </>
    );
}
