import "particles.js";
import { Navbar } from "./Navbar";
import { SocialIcons } from './SocialIcons';
import wasif_img from "./../assets/wasif0.png";
import { useTranslation } from "react-i18next";
import React, { useEffect, useState } from "react";
import { TypeAnimation } from 'react-type-animation';
import { Helmet } from "react-helmet-async";


export function About() {
  useEffect(() => {
    window.particlesJS("particles-js", {
      particles: {
        number: {
          value: 480,
          density: {
            enable: true,
            value_area: 2000,
          },
        },
        color: {
          value: "#4da7ce",
          opacity: 0,
        },
        shape: {
          type: "circle",
          stroke: {
            width: 2,
            color: "#71858d",
            opacity: 0,
          },
          polygon: {
            nb_sides: 2,
          },
        },
        opacity: {
          value: 0.1,
          anim: {
            enable: false,
          },
        },
        size: {
          value: 2,
          random: true,
          anim: {
            enable: false,
          },
        },
        line_linked: {
          enable: true,
          distance: 100,
          color: "#5ecaf4",
          opacity: 0.5,
          width: 1,
        },
        move: {
          enable: true,
          speed: 6,
          out_mode: "out",
        },
      },
      interactivity: {
        detect_on: "canvas",
        events: {
          onhover: { enable: true, mode: "grab" },
          onclick: { enable: true, mode: "push" },
        },
        modes: {
          grab: { distance: 140, line_linked: { opacity: 0.5 } },
          push: { particles_nb: 4 },
        },
      },
      retina_detect: true,
    });

    return () => {
      const particlesCanvas = document.getElementById("particles-js");
      if (particlesCanvas) particlesCanvas.innerHTML = "";
    };
  }, []);

  const { t, i18n } = useTranslation();
  const [sequence, setSequence] = useState([]);
  const [key, setKey] = useState(0);
  useEffect(() => {
    const translatedSequence = t("about.heading", { returnObjects: true });
    setSequence(translatedSequence)
    setKey((prevKey) => prevKey + 1);
  }, [i18n.language, t])

  return (
    <div className="text-white relative lg:px-0">
      <Helmet>
        <meta name="description" content={t("meta.description")} />
        <meta name="keywords" content={t("meta.keywords")} />
        <meta name="author" content={t("meta.author")} />
      </Helmet>
      <div
        id="particles-js"
        style={{ position: "absolute", width: "100%", height: "100%", zIndex: -1 }}
      ></div>

      <Navbar />
      <div className="mt-6 lg:mt-0" style={{ position: "relative", zIndex: 1 }}>
        <p className="w-[85%] m-auto text-xl text-center font-bold lg:py-8 md:text-4xl lg:text-5xl">
          <TypeAnimation
            key={key}
            sequence={sequence}
            wrapper="span"
            speed={20}
            deletionSpeed={0}
            omitDeletionAnimation={true}
            repeat={0}
          />
        </p>
      </div>

      <div className="flex flex-col md:flex-row justify-center items-center px-4 py-10">
        <div className="flex justify-center items-center mb-10 md:mb-0 md:w-4/12">
          <img
            className="rounded-full w-40 md:w-56 lg:w-96 border-2 border-[#5ecaf4]"
            src={wasif_img}
            alt="Wasif Shehzad"
          />
        </div>

        <div className="text-center md:text-left md:w-9/12 md:px-6">
          <h1 className="font-arial text-xl md:text-2xl lg:text-3xl font-extrabold text-white">
            {t("about.name")}
          </h1>
          <h2 className="font-arial  text-sm md:text-lg lg:text-xl font-semibold">
            {t("about.title")}
          </h2>

          <div className="text-sm text-justify px-2 lg:px-0 md:text-base lg:text-lg text-white mt-4 lg:pr-24">
            <p>{t("about.description")}</p>
            <p className="py-4">{t("about.description1")}</p>
          </div>
          <SocialIcons />
        </div>
      </div>
    </div>
  );
}
