import React, { useRef } from 'react';
import Autoplay from "embla-carousel-autoplay";
import { Carousel, CarouselContent, CarouselItem } from './ui/Carousel';
import { useTranslation } from 'react-i18next';
import { Helmet } from 'react-helmet-async';

function Testimonials() {
    const autoplay = useRef(Autoplay({ delay: 3000, stopOnInteraction: false })); // Autoplay every 3 seconds
    const { t } = useTranslation();
    const testimonials = t('testimonials.items', { returnObjects: true });

    const handleMouseEnter = () => {
        autoplay.current?.stop(); // Stop autoplay on hover
    };

    const handleMouseLeave = () => {
        autoplay.current?.play(); // Resume autoplay when hover ends
    };

    return (
        <div>
            <Helmet>
                <meta name="description" content={t("meta.description")} />
                <meta name="keywords" content={t("meta.keywords")} />
                <meta name="author" content={t("meta.author")} />
            </Helmet>
            <p className="text-3xl text-center font-bold sm:py-20 md:py-20 lg:py-20 md:text-4xl lg:text-5xl">
                {t('testimonials.title')}
            </p>
            <div 
                className="flex justify-center items-center md:px-12 lg:px-16"
                onMouseEnter={handleMouseEnter}
                onMouseLeave={handleMouseLeave}
            >
                <Carousel
                    plugins={[autoplay.current]}
                    className="sm:max-w-full lg:max-w-full lg:px-8 rounded-lg"
                >
                    <CarouselContent className="flex">
                        {testimonials.map((testimonial, index) => (
                            <CarouselItem key={index} className="flex-none xs:w-full sm:w-full lg:w-1/3 sm:px-8  lg:px-4 ">
                                <div className="flex flex-col h-full bg-transparent text-white border-b border-[#5ecaf4] rounded-sm">
                                    <div className="flex-grow px-4">
                                        <p className="mt-4 text-left">{testimonial.description}</p>
                                    </div>
                                    <div className="px-4 py-4 mt-auto">
                                        <h3 className="text-xl font-bold">{testimonial.name}</h3>
                                        <p className="text-md">{testimonial.designation}</p>
                                    </div>
                                </div>
                            </CarouselItem>
                        ))}
                    </CarouselContent>
                </Carousel>
            </div>
        </div>
    );
}

export default Testimonials;
